<template>
  <b-row>
    <b-container>
       <h1>Информация о переливании</h1>
    </b-container>
    <b-col cols="12">
        <div class="card">
          <h5 class="card-header"><b>Группа крови: </b><span v-if="trans.bloodType">{{trans.bloodType.typeName}}</span></h5>
          <div class="card-body">
            <p v-if="trans.source"><b>Материал:</b> {{trans.source.sourceName}} ({{trans.source.measure}})</p>
            <p v-if="trans.volume"><b>Объем:</b> {{trans.volume}}</p>
            <p v-if="trans.date"><b>Дата переливания:</b> {{trans.date}}</p>
            <p v-if="trans.draw"><b>Дата забора:</b> <b-link href="#" v-bind:to="{name: 'DrawShow', params: {id: trans.draw._id}}">{{trans.draw.date}}</b-link></p>
            <p v-if="trans.recipient"><b>Реципиент:</b> <b-link href="#" v-bind:to="{name: 'PersonShow', params: {id: trans.recipient._id}}">{{trans.recipient.firstName}} {{trans.recipient.lastName}}</b-link></p>
            <p v-if="trans.draw"><b>Донор:</b> <b-link href="#" v-bind:to="{name: 'PersonShow', params: {id: trans.draw.person._id}}">{{trans.draw.person.firstName}} {{trans.draw.person.lastName}}</b-link></p>
            <p v-if="trans.description"><b>Доп.информация:</b> {{trans.description}}</p>
            <b-button v-bind:to="{ name: 'TransAdd', params: {id: trans._id }}" variant="primary">Сделать новое переливание</b-button>
            <b-button v-bind:to="{ name: 'Trans' }" variant="primary">Список переливаний</b-button>
          </div>
        </div>
    </b-col>
  </b-row>
</template>

<script>
import TransService from '@/services/TransService'
/*id: person._id*/
export default {
  name: 'TransShow',
  data () {
    return {
      trans: {}
    }
  },
  methods: {
    async getTrans() {
      const response = await TransService.getTrans({ id: this.$route.params.id });

      if(response.data.success !== false){
        this.trans = response.data.data;
      }
    }
  },
  created: function() {
  },
  mounted () {
    this.getTrans()
  },
}
</script>
<style>
</style>




