import Api from '@/services/Api'

export default {
  fetchDraws () {
    return Api().get('api/draw');
  },
  addDraw(params) {
    return Api().post('api/draw', params);
  },
  getDraw(params) {
    return Api().get('api/draw/' + params.id);
  },
  updateDraw(params) {
    return Api().put('api/draw/' + params.id, params);
  }
}



