  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
.container-fluid {
  margin-right: auto;
  margin-left: auto;
  max-width: 1024px; /* or 950px */
}
.card-body p{
  margin-bottom: .3em;
}
.card-body button, .card-body.btn, .card-body.btn-primary{
  margin-top: 1em;
}
</style>



