<template>
  <b-row>
    <b-col cols="12">
  		<h1>Заборы крови</h1>
      <b-table striped :stacked="stacked" small hover :items="draws" :fields="fields">
        <template slot="index" slot-scope="data">
          {{data.index + 1}}
        </template>
        <template slot="source" slot-scope="data">
          {{data.value.sourceName}} ({{data.value.measure}})
        </template>
        <template slot="bloodType" slot-scope="data">
          {{data.value.typeName}} ({{data.value._id}})
        </template>
        <template slot="person" slot-scope="data">
          {{data.value.fullName}}
        </template>
        <template slot="actions" slot-scope="row">
          <b-btn v-if="mode !== 'drawselect'" size="sm" v-bind:to="{ name: 'DrawShow', params: {id: row.item._id }}">просм.</b-btn>
          <b-btn v-if="mode !== 'drawselect'" size="sm" v-bind:to="{ name: 'TransAdd', params: {id: row.item._id }}">перелив.</b-btn>
          <b-btn v-if="mode==='drawselect'" @click="selectDraw(row.item)" size="sm">выбрать</b-btn>
        </template>        
  		</b-table>
    </b-col>
  </b-row>
</template>

<script>
import DrawService from '@/services/DrawService'
import BloodTypeService from '@/services/BloodTypeService'

export default {
  name: 'Persons',
  data () {
    return {
      fields: [
          { key: 'index', label: "#"},
          { key: 'date', label: 'Дата'},
          { key: 'source', label: 'Материал'},
          { key: 'volume', label: 'Объем'},
          { key: 'remainder', label: 'Остаток'},
          { key: 'bloodType', label: 'Группа крови'},
          { key: 'person', label: 'Донор'},
          { key: 'actions', label: 'Action'}
        ],

    	draws: [],
    	bloodTypes: []
    }
  },
  computed: {
    stacked(){
      return this.mode === 'drawselect';
    }
  },
  props: ['mode'],
  methods: {
    async getDraws() {
      const response = await DrawService.fetchDraws();
      this.draws = response.data.data.map(draw => {
          draw.person.fullName = this.$tools.smartJoin([
            draw.person.firstName, draw.person.lastName
          ]);
          return draw;
      });
    },
    async getBloodTypes(){
      const response = await BloodTypeService.fetchBloodTypes();
        this.bloodTypes = response.data.data;
    },
    selectDraw(person){
      this.$emit('selectdraw', person);
    }
  },
  created: function() {
    this.getDraws()
  	this.getBloodTypes();
  }
}
</script>



