<template>
  <b-row>
    <b-col cols="12">
  		<h1>Переливания крови</h1>
      <b-table striped small hover :items="trans" :fields="fields">
        <template slot="index" slot-scope="data">
          {{data.index + 1}}
        </template>
        <template slot="source" slot-scope="data">
          {{data.value.sourceName}} ({{data.value.measure}})
        </template>
        <template slot="recipient" slot-scope="data">
          {{data.value.fullName}} <br/>{{data.value.bloodType.typeName}}
        </template>
        <template slot="bloodType" slot-scope="data">
          {{data.value.typeName}} ({{data.value._id}})
        </template>
        <template slot="draw" slot-scope="data">
          {{data.value.date}} <br/>{{data.value.person.firstName}} {{data.value.person.lastName}}
        </template>
        <template slot="actions" slot-scope="row">
          <b-btn size="sm" v-bind:to="{ name: 'TransShow', params: {id: row.item._id }}">
            просмотреть
          </b-btn>
        </template>        
  		</b-table>
    </b-col>
  </b-row>
</template>

<script>
import TransService from '@/services/TransService'
import BloodTypeService from '@/services/BloodTypeService'

export default {
  name: 'Persons',
  data () {
    return {
      fields: [
          { key: 'index', label: "#"},
          { key: 'date', label: 'Дата'},
          { key: 'recipient', label: 'Реципиент' },
          { key: 'source', label: 'Материал'},
          { key: 'volume', label: 'Объем' },
          { key: 'bloodType', label: 'Группа крови' },
          { key: 'draw', label: 'Дата забора' },
          { key: 'actions', label: 'Action'}
        ],
    	trans: [],
    	bloodTypes: []
    }
  },
  methods: {
    async getTrans() {
      const response = await TransService.fetchTrans();
      this.trans = response.data.data.map(tran => {
          tran.recipient.fullName = this.$tools.smartJoin([
            tran.recipient.firstName, tran.recipient.lastName, tran.recipient.middleName
          ]);
          return tran;
      });
    },
    async getBloodTypes(){
      const response = await BloodTypeService.fetchBloodTypes();
        this.bloodTypes = response.data.data;
    }
  },
  created: function() {
    this.getTrans()
  	this.getBloodTypes();
  }
}
</script>



